.admin-container {
  height: 100%;
  max-width: 800px;
  margin: 20px auto;

}
.head1 {
  background-color: #343a40; /* Dark gray header */
  color: white; /* White text color */
  padding: 5px; /* Padding around header */
  text-align: center; /* Center the text */
}

.admin-container h2 {
  text-align: center;

}

section{
  height: 100%;
}

section h3 {
  border-bottom: 2px solid #2980b9;
  padding-bottom: 10px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  padding: 10px;
  border: 1px solid #eee;
  border-radius: 4px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  background-color: black;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #1c6699;
}

h4 {
  margin-top: 20px;
 
}

.user-interaction {
  border: 1px solid #ddd;
  padding: 10px;
  margin: 10px 0;
  border-radius: 4px;
  background-color: #f5f5f5;
}

.user-interaction p {
  margin: 5px 0;
}

.user-interaction ul {
  margin-left: 20px;
}

input[type="text"], input[type='password'],select {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
} 

input[type="text"]:focus , input:focus, select:focus {
  border-color: #2980b9;
  outline: none;
}

/* In your Admin.css or a relevant CSS file */
.referral-list {
  list-style: none; /* Remove default list styling */
  padding: 0; /* Remove padding from the list */
}

.admin-referral-details{
  margin-top: 20px;

}

.referral-item {
  display: flex; 
  flex-direction: column;/* Use flexbox for alignment */
  justify-content: space-between; /* Space out the items */
  align-items: center; /* Center items vertically */
  padding: 10px; /* Add padding around each item */
  border-bottom: 1px solid #ccc;
  row-gap: 16px; /* Add a bottom border for separation */
}

.referral-info {
  /* display: flex; */
  flex: 1; /* Allow the info section to take up the available space */
  /* flex-direction: row; */
  /* flex-wrap: wrap; */
  /* gap:8px; */
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  padding:0 16px;
  justify-items: stretch;
  row-gap: 8px;
  /* justify-content: space-between; */
  /* align-items: start; */
  flex-grow: 1; /* Allow the info section to take available space */
  /* margin-right: 20px; Space between text and button */
  font-size: 16px; /* Font size for the text */
}
.referral-bold{
  font-weight: bold;
}
.referral-bold span{
  font-weight: normal;
}
.view-details-button {
  padding: 8px 12px; /* Padding inside the button */
  background-color: black; /* Button background color */
  color: white; /* Button text color */
  border: none; /* Remove default border */
  border-radius: 4px; /* Rounded corners */
  cursor: pointer; /* Pointer cursor on hover */
  transition: background-color 0.3s; /* Smooth background color transition */
}

.view-details-button:hover, .view-details-button:active{
  background-color: #0056b3; /* Darker shade on hover */
}
span{
  color:black;
}
button:disabled {
  cursor: not-allowed; /* Optional: changes the cursor to indicate non-interactivity */
  opacity: 0.5;
}


.pagination-controls{
  display:flex;
  justify-content: space-around;
  align-items: center;
}
.interaction-pagination-controls{
  display:flex;
    align-items: center;
    justify-content: space-around;
}

.interaction-pagination-controls button {
    width: fit-content;
}

