/* Admin.css */

body {
  font-family: Arial, sans-serif; /* Set a nice default font */
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
  background-color: #f8f9fa; /* Light background color */
}

.head {
  background-color: #343a40; /* Dark gray header */
  color: white; /* White text color */
  padding: 5px; /* Padding around header */
  text-align: center; /* Center the text */
}

.Influencer {
  margin: 20px; /* Margin around the main content */
  padding: 20px; /* Padding inside the content */
  border-radius: 5px; /* Rounded corners */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  display: flex; /* Enable flexbox */
  flex-direction: column; /* Arrange items in a column */
  align-items: center; /* Center align items horizontally */
}

.influencer-Container{
  display: flex;
  flex-direction: column;
  align-items: center;
}
h1, h2, h3 {
  margin: 10px 0; /* Margin for headings */
}

.Influencer button {
  background-color: black; /* Set button color to #337ab7 */
  color: white; /* White text */
  border: none; /* Remove default border */
  padding: 10px 15px; /* Padding inside buttons */
  border-radius: 20px; /* Rounded corners */
  cursor: pointer; /* Pointer cursor on hover */
  transition: background-color 0.3s; /* Smooth transition for hover effect */
  margin-top: 20px; /* Space above button */
  width:300px;
}

.Influencer button:hover {
  background-color: #d0d9e1;  /* Darker blue on hover */
}
hr {
  margin: 20px 0; /* Space around horizontal rule */
  border: none; /* Remove default border */
  border-top: 1px solid #dee2e6; /* Light gray top border */
}

#promo {
  font-size: 35px;
}

.share-icons {
  display: flex;
  justify-content: center; /* Centers the icons horizontally */
  align-items: center; /* Centers them vertically in the container */
  gap: 20px; /* Adds space between icons */
  margin-top: 20px; /* Adds space above the icons */
}

#icon {
  font-size: 40px; /* Size of the icons */
  cursor: pointer;
  transition: transform 0.3s ease;
}

#icon:hover {
  transform: scale(1.2); /* Makes the icon slightly larger on hover */
  color: #007bff; /* Changes color on hover (you can modify this color) */
}


.referral-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensure popup is on top */
}

.popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
  width: 50%;
  position: relative;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  cursor: pointer;
}

.close-button:hover {
  color:black;
}

.promo-code-display {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 1rem;
}

.share-icons {
  margin-top: 1rem;
}

.share-icons i {
  font-size: 2rem;
  margin: 0 10px;
  cursor: pointer;
}

.share-icons i:hover {
  color: white;
}



/* General container styling */
.Influencer {
  margin: 20px;
}

/* Referral details container */
.referral-details {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
}

/* Individual referral item box */
.referral-item {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease;
}

/* Hover effect for referral box */
.referral-item:hover {
  transform: translateY(-3px);
}

/* Title and text styling */
.referral-item p {
  margin: 5px 0;
  color: #333;
}

.referral-item p strong {
  color: #555;
}

/* Optional button styling inside referral boxes */
.referral-item button {
  margin-top: 10px;
  padding: 8px 12px;
  font-size: 14px;
  color: #fff;
  background-color: black;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.referral-item button:hover {
  background-color: #d0d9e1;
}
.referral-item button:disabled{
  cursor: not-allowed; /* Optional: changes the cursor to indicate non-interactivity */
  opacity: 0.5;
  
}


.Influencer input[type="text"] {
  padding: 8px;
  width: 600px;
  margin-right: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
}

/* Styling for referral details modal */
.referral-details-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 700px;
  max-height: 80vh; /* Set a maximum height for the modal */
  overflow-y: auto; /* Enable vertical scrolling */
  transform: translate(-50%, -50%);
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  z-index: 1000;
}

.referral-details-modal h3 {
  text-align: center;
  margin-bottom: 15px;
  color: #333;
  font-size: 18px;
}


/* Styling for the referral details table */
.referral-details-table {
  width: 100%;
  border-collapse: collapse;
  margin: 15px 0;
}

.referral-details-table td {
  padding: 10px 15px;
  border: 1px solid #ddd;
  text-align: left;
  font-size: 14px;
  color: #555;
}

.referral-details-table td:first-child {
  font-weight: bold;
  background-color: #f3f4f6;
  color: #333;
}

.referral-details-table td:nth-child(2) {
  color: #333;
}

/* Close button styling */
.referral-details-modal button {
  display: block;
  margin: 20px auto 0; /* Centered alignment */
  padding: 8px 20px;
  font-size: 14px;
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.referral-details-modal button:hover {
  background-color: #d0d9e1;
  color: #333;
}


/* Referred Users Table Styling */
.referred-users-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  font-size: 16px;
  color: #333;
}

.referred-users-table th,
.referred-users-table td {
  padding: 12px;
  border: 1px solid #ddd;
  text-align: left;
}

.referred-users-table thead th {
  background-color: black;
  color: white;
  font-weight: bold;
}

.referred-users-table tbody tr:nth-child(even) {
  background-color: #f9f9f9; /* Light gray for alternating rows */
}

.referred-users-table tbody tr:hover {
  background-color: #f1f1f1; /* Slight hover effect */
}


#view-details{
  margin-left: 650px;
}

.Influencer input[type="text"]:focus {
  border-color: black;
  outline: none;
}

.influencer-interaction-pagination-controls{
  display:flex;
  align-items: center;
  width: 100%;
  justify-content: space-around;
}
/* .influencer-interaction-pagination-controls button{
  width: fit-content;
} */
.influencer-interaction-pagination-controls span{
  text-align: center;
}

/* Responsive css */

/* Responsive Styles */

/* Mobile Devices */
@media (max-width: 480px) {
  .Influencer {
      margin: 10px; /* Reduce margin */
      padding: 10px; /* Reduce padding */
  }

  .Influencer input[type="text"] {
      width: 100%; /* Full width on mobile */
      margin-right: 0; /* Remove right margin */
  }

  #view-details {
      margin-left: 0; /* Center content */
  }

  .summary-box,
  .referral-item,
  .popup-content {
      width: 90%; /* Use full width on mobile */
      margin: 0 auto; /* Center items */
  }

  .referral-details-modal {
      width: 90%; /* Modal full width on mobile */
  }

  .Influencer button {
      width: 100%; /* Full width for buttons on mobile */
  }
}

/* Tablets */
@media (min-width: 481px) and (max-width: 768px) {
  .Influencer {
      margin: 15px; /* Moderate margin for tablets */
      padding: 15px; /* Moderate padding */
  }

  .summary-box,
  .referral-item,
  .popup-content {
      width: 95%; /* Adjust width for tablets */
      margin: 0 auto; /* Center items */
  }

  .referral-details-modal {
      width: 80%; /* Modal width for tablets */
  }

  .Influencer input[type="text"] {
      width: 100%; /* Full width */
  }

  .Influencer button {
      width: 100%; /* Full width for buttons */
  }

  #view-details {
      margin-left: 20px; /* Adjust margin for tablets */
  }
}

/* Desktops */
@media (min-width: 769px) {
  .Influencer {
      margin: 20px; /* Standard margin for desktops */
  }

  .referral-details-modal {
      width: 700px; /* Set width for desktops */
  }

  #view-details {
      margin-left: 650px; /* Maintain original margin */
  }
}

