.User {
    font-family: Arial, sans-serif;
    text-align: center;
    padding: 20px;
  }
  
.User input {
    padding: 10px;
    margin: 10px;
    width: 40%;
  }
  
  button {
    padding: 10px 20px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    width: 200px;
    font-size: 18px;
  }
  
  .success {
    color: green;
    font-weight: bold;
    margin-top: 20px;
  }

  .head{
    background-color: black;
    padding: 5px;
  }

  #Refcodebtn{
    margin: 20px;
    padding: 10px 20px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    width: 250px;
    font-size: 18px;
  }

/* Center the generated code container */
.generated-code {
  margin: 20px auto;            /* Centers the container horizontally */
  padding: 10px;
  height: 100px;
  width: 300px;
  background-color: #f5f5f5;    /* Light gray background */
  border: 1px solid #ccc;       /* Light border for separation */
  border-radius: 5px;
  text-align: center;
  display: flex;                /* Flexbox for vertical centering */
  align-items: center;          /* Center vertically */
  justify-content: center;      /* Center content horizontally */
}

.generated-code p {
  font-size: 16px;
  color: #333;                  /* Darker font color */
  margin: 0;
}

.generated-code strong {
  color: #d4af37;               /* Gold color to highlight the referral code */
  font-weight: bold;
}


input:focus {
  border-color: black;
  outline: none;
}
.card{
  cursor: pointer;
}

.card-container{
  margin-top:50px;
}
.card img{
  width: 100%;
  object-fit: fill;
}