.login-signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%; /* Full viewport height */
  width: 100%; /* Full viewport width */
  background-color: #f0f0f0; 
  min-height: inherit;
  height: inherit;/* Light gray background */
}

.login-signup-form {
  width: 100%;
  max-width: 400px; /* Max width for responsiveness */
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.login-signup-form form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.role-select{
  width: 100%;
  padding:10px;
  margin:10px;
  border: 1px solid #ccc;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

input {
  width: 100%;
  height: 45px;
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  transition: border-color 0.3s ease;
}

input:focus, select:focus {
  border-color: black;
  outline: none;
}

button[type="submit"] {
  width: 100%;
  height: 45px;
  background-color: black;
  color: #fff;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
  background-color: #d0d9e1;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
button[type="submit"]:disabled , .google-button:disabled{
  /* background-color: grey; */
  cursor: not-allowed; /* Optional: changes the cursor to indicate non-interactivity */
  opacity: 0.5; /* Optional: makes the button look visually disabled */
}

.google-button {
  width: 100%;
  height: 45px;
  background-color: black;
  border: 1px solid #ccc;
  border-radius: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  color: white;
  transition: background-color 0.3s ease;
}

.google-button:hover {
  background-color: #d0d9e1;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.google-button img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

p {
  margin-top: 20px;
  font-size: 14px;
  color: black;
  text-align: center;
}

.login-signup-container span {
  color: #337ab7;
  cursor: pointer;
}

.toggle-buttons {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.toggle-buttons button {
  flex: 1;
  margin: 0 5px;
  padding: 10px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  background-color: black;
  color: white;
  transition: background-color 0.3s ease;
}

.toggle-buttons button:hover {
  background-color: #d0d9e1;
}

.toggle-buttons button.active {
  background-color: white;
  color: black;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

