/* General Styles */
.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 100px;
}

.card {
  width: 18rem;
  margin: 10px;
}

.card a {
  color: white;
  text-decoration: none;
  height: 20px;
  width: 30px;
  background-color: #337ab7;
  padding: 5px;
  border-radius: 5px;
}

.content{
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.container1 {
  display: flex;
  gap:2rem;
  flex-direction: row;
  align-items: center;
  padding: 40px;
  min-height: inherit;
  height: inherit;
/* Add this to position the content on top of the image */
}
.wave{
  min-height: inherit;
  padding: 40px;
  height: 100%; 
  width: 100%;
  background-image: url('./Necklace.png'); /* Add your background image URL here */
  background-size: cover;
  background-position: center;
  height: 100%; /* Make the container full height */
  position: relative;
}
.box {
  height: 350px;
  width: 800px;
  background-color: rgba(255, 255, 255, 0.5);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
/* footer{
