.heading {
  color:black;
  font-size: 36px;
  font-weight: bold;
  position: relative;
  top: 20px;
}

.slogan {
  color:black;
  top: 15px;
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 10px;
  position: relative;
}

.promo-button p {
  color:black;
  position: relative;
  font-size: 25px;
  font-weight: bold;
  margin: 0;
}

.promo-button {
  margin-top: 30px;
}

.promo-button button {
  padding: 10px 20px;
  font-size: 18px;
  border-radius: 20px;
  background-color: black;
  color: #fff;
  border: none;
  cursor: pointer;
  font-weight: bold;
  position: relative;
}

.nav-link:hover {
  color: coral;
}

.log {
  background-color: white;
  margin-right: 15px;
  border: none;
  width: 100px;
  font-size: 15px;
  border-radius: 20px;
}

.sign {
  background-color: black;
  border: none;
  width: 100px;
  font-size: 15px;
  border-radius: 20px;
}

/* Responsive Styles */

/* Mobile Devices */
@media (max-width: 480px) {
  .container1 {
      padding: 20px; /* Reduced padding */
 /* Reduced height */
   
  }

  .box {
      height: auto; /* Auto height for smaller screens */
      width: 90%; /* Full width on mobile */
  }

  .heading {
      font-size: 28px; /* Smaller font size */
  }

  .slogan {
      font-size: 20px; /* Smaller font size */
  }

  .promo-button p {
      font-size: 20px; /* Smaller font size */
  }

  .promo-button button {
      padding: 8px 16px; /* Reduced padding */
      font-size: 16px; /* Smaller button text */
  }

  .card {
      width: 100%; /* Full width cards */
      margin: 5px 0; /* Vertical margin */
  }
}

/* Tablets */
@media (min-width: 481px) and (max-width: 768px) {
  .container1 {
      padding: 30px; /* Moderate padding */
      /* Moderate height */
  }

  .box {
      height: 300px; /* Moderate height */
      width: 80%; /* Wider box */
  }

  .heading {
      font-size: 32px; /* Moderate font size */
  }

  .slogan {
      font-size: 22px; /* Moderate font size */
  }

  .promo-button p {
      font-size: 22px; /* Moderate font size */
  }

  .promo-button button {
      padding: 9px 18px; /* Moderate button padding */
      font-size: 17px; /* Moderate button text */
  }

  .card {
      width: 40%; /* Two cards per row */
      margin: 10px; /* Adjusted margin */
  }
}

/* Desktops */
@media (min-width: 769px) {
  .container1 {
      padding: 40px; /* Standard padding */
    /* Full height */
  }

  .box {
      height: 350px; /* Standard height */
      width: 800px; /* Standard width */
  }

  .heading {
      font-size: 36px; /* Standard font size */
  }

  .slogan {
      font-size: 25px; /* Standard font size */
  }

  .promo-button p {
      font-size: 25px; /* Standard font size */
  }

  .promo-button button {
      padding: 10px 20px; /* Standard button padding */
      font-size: 18px; /* Standard button text */
  }

  .card {
      width: 18rem; /* Standard card width */
      margin: 10px; /* Standard margin */
  }
}

